<template>
	<view class="result-page-container">
		<view class="page-nav">
			<view>
				<u-icon name="arrow-left" @click="back" size="18px"></u-icon>
			</view>
			<view>
				<u-search v-model="keyWord" :show-action="true" :showAction="false" :disabled="true" @click="seach">
				</u-search>
			</view>
		</view>
		<u-tabs :list="topTabs" lineWidth="30" lineColor="#2979ff" :activeStyle="{
			color: '#303133',
			fontWeight: 'bold',
			transform: 'scale(1.05)'
		}" :inactiveStyle="{
			color: '#606266',
			transform: 'scale(1)'
		}" itemStyle="padding-left: 15px; padding-right: 15px; height: 34px;"
		@change="tabChange">
		</u-tabs>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				keyWord: '',
				topTabs: [{
					key:1018,
					name: '综合',
				}, {
					key:1,
					name: '单曲',
					// badge: {
					// 	isDot: true
					// }
				}, {
					key:10,
					name: '专辑',
				}, {
					key:100,
					name: '歌手'
				}, {
					key:1000,
					name: '歌单'
				}, {
					key:1004,
					name: 'MV'
				}],
			}
		},
		onLoad(options) {
			this.keyWord = options?.keyWords;
		},
		methods: {
			back() {
				uni.navigateBack({
					delta: 1
				})
			},
			seach() {
				uni.navigateTo({
					url: `../seachIndex/seachIndex?keyWord=${this.keyWord}`
				})
			},
			tabChange(item){
				console.log(item)
			}
		}
	}
</script>

<style lang="scss">
	.result-page-container {
		position: relative;

		>.page-nav {
			position: sticky;
			left: 0;
			top: 0;
			width: 100%;
			height: 50px;
			display: flex;
			justify-content: center;
			align-items: center;

			>view {
				display: flex;
				justify-content: center;
				align-items: center;
			}

			>view:first-child {
				width: 30px;
			}

			>view:last-child {
				flex: 1;
				position: relative;
			}
		}
	}
</style>
